body {
  --dark-grey-1: #1c1c1c;
  --white-1: #fcfcfc;
}
.hidden {
  display: none !important;
}

.hs-search-field {
  width: 100%;
  max-width: none;

  form {
    display: flex;
    justify-content: center;
  }
}

.hs-search-field input {
  width: 100%;
  max-width: 559px;
  border-radius: 140px;
  border: 2px solid #1c1c1c;
  background: #fcfcfc;
  box-shadow: -6px 6px 0px 0px rgba(28, 28, 28, 0.1) inset;
  padding: 22px 37px 22px 30px;
  position: relative;
  font-size: 24px;
  font-style: normal;
  color: #1c1c1c;

  &::placeholder {
    color: #1c1c1c;
    opacity: 0.3;
  }

  &:before {
    background-image: url("https://cdn.jsdelivr.net/gh/Dev-DigiFresh/BlogStylesBuild/img/search.svg");
    content: "";
  }
}

.hs-search-field__bar:after {
  display: none;
}

.blog-tags {
  margin: 38px auto 0;
  width: 100%;
  text-align: center;
  max-width: 705px;

  li {
    margin: 6px 3px 0 0;
  }
}

.blog-tags__link {
  border-radius: 70px;
  border: 2px solid #1c1c1c;
  background: #fcfcfc;
  color: #1c1c1c;
  opacity: 0.3;
  padding: 8px 24px;

  &:hover,
  &.blog-tags__link--current {
    opacity: 1;
    transform: none;
    border: 2px solid #1c1c1c;
    background-color: #fcfcfc;
    color: #1c1c1c;
  }
}

.blog-index__post {
  border-radius: 20px;
  border: 2px solid var(--dark-grey-1, #1c1c1c);
  background: var(--white-1, #fcfcfc);
  box-shadow: -6px 6px 0px 0px rgba(28, 28, 28, 0.1);
  padding: 20px;
}

.blog-index__post-image,
.blog-more-posts__post-image {
  width: 100%;
  height: 170px;
}

.blog-index__post-content {
  padding: 0;
}

.blog-index__post-meta .blog-index__post-author {
  display: none;
}
